.sceleton {
  width: 100%;
  background-color: #fcfcfc;
  position: relative;
  overflow: hidden;

  &::after {
    content: "";
    position: absolute;
    inset: 0;
    background-image: linear-gradient(
      90deg,
      rgba(#ccc, 0) 0,
      rgba(#ccc, 0.2) 25%,
      rgba(#ccc, 0.5) 50%,
      rgba(#ccc, 0.2) 75%,
      rgba(#ccc, 0) 100%
    );
    animation: skeleton 1.5s linear infinite alternate;
  }
}

@keyframes skeleton {
  0% {
    transform: translateX(-50%);
  }
  100% {
    transform: translateX(50%);
  }
}
